      h1 Medicin de presin sonora y olores


    .row.fondo-tema2.align-items-center.py-5.mb-5
      .col-lg-12.col-12.px-lg-5.px-4
        .row.justify-content-center.align-items-center
          
          p.mb-4(data-aos="fade") Antes de profundizar en los equipos de medicin se hace necesario definir algunos conceptos bsicos y relevantes que se emplean en las toma de muestras de ruido.
          
          .col-lg-3.col-md-5.col-7.mb-4.px-md-0(data-aos="fade-right")
            img(src='@/assets/curso/temas/tema2/img.png' alt='')
          .col-lg-7.mb-4.px-md-0(data-aos="fade-left")
            SlyderE.bg-white.box-shadow.p-4.px-md-5(indicadores="derecha")
              .row
                h5 Sonido   
                p Es un fenmeno fsico que consiste en la alteracin mecnica de las partculas de un medio elstico, producida por un elemento en vibracin, que es capaz de provocar una sensacin auditiva. (sicaweb.cedex.es, s.f.)
              .row
                h5 Ruido   
                p Se define como aquel sonido no deseado. Es aquella emisin de energa originada por un fenmeno vibratorio al igual que el sonido, que es detectado por el odo pero que en diferencia con este, el ruido provoca una sensacin de molestia. (sicaweb.cedex.es, s.f.). Existen tres tipos de ruido: 
                p.mb-0 #[b Continuo:] se presenta cuando el nivel de presin sonora es prcticamente constante durante el periodo de observacin. 
                p.mb-0 #[b Intermitente:] producen cadas bruscas hasta el nivel ambiental de forma intermitente, volvindose a alcanzar el nivel superior.
                p.mb-0 #[b Impacto:] se caracteriza por una elevacin brusca de ruido en un tiempo inferior a 35 milisegundos y una duracin total de menos de 500 milisegundos.
              .row
                h5 Presin sonora    
                p Una fuente sonora produce una cierta cantidad de energa por unidad de tiempo, esto es una cierta potencia sonora.
              .row
                h5 Decibelio (dB)   
                p Unidad logartmica de medida utilizada en diferentes disciplinas de la ciencia usada para comparar una cantidad con otra llamada de referencia. Adicionalmente este concepto que en ocasiones se puede tomar un valor medio aproximado. En Acstica la mayora de las veces el decibelio se utiliza para comparar la presin sonora, en el aire, con una presin de referencia. (Construmatica, 2010)
              .row
                h5 Pascal (Pa)   
                p La presin atmosfrica es la presin del aire ambiental en ausencia de sonido y este se mide en una unidad denominada Pascal. 1 Pascal es igual a una fuerza de 1 newton actuando sobre una superficie de 1 metro cuadrado, y se abrevia 1 (Pa).
                p.fw-bold.mb-0 Factor de Conversin Pa a dB.
                p.mb-0 #[b 1(Pa)] = 93.979 (dB)
                p #[b 1(dB)] = 0.00002244037 (Pa) = 2.244X100<sup>-5</sup> (Pa)
              .row
                h5 Logaritmo    
                p Un logaritmo expresa potenciacin, es decir, indica el exponente por el cual se debe elevar la base para obtener la potencia indicada, matemticamente se puede expresar de la siguiente manera: 
                p El logaritmo en base  
                  b b 
                  | de un nmero 
                  b a&gt;
                  | 0 se representa por 
                  b log<sub>b</sub>(a) 
                  | y es el nmero c que cumple 
                  b b<sup>c</sup>=a 
                p.fw-bold log<sub>b</sub>(a) = c <=&gt; b<sup>c</sup> = a
              .row
                h5 Suma de logaritmos    
                p El logaritmo del producto de dos nmeros positivos es igual a la suma de los logaritmos de dichos nmeros. Esto es:
                p.fw-bold log(a . b) = log(a) + log (b)
              .row
                h5 Resta de logaritmos    
                p El logaritmo del cociente de dos nmeros positivos es igual al logaritmo del numerador menos el logaritmo del denominador. Esto es:
                p.fw-bold log ( a / b ) = log(a) - log (b)
              .row
                h5 Estadstica    
                p Ciencia que se encarga de la recoleccin, ordenamiento, anlisis e interpretacin de datos recopilados en una investigacin sobre hechos, individuos o grupos, para deducir de ello conclusiones precisas o estimaciones futuras.
              .row
                h5 Poblacin y muestra    
                p Es un conjunto de individuos al que se desea describir o queremos estudiar. La muestra es un conjunto de elementos seleccionados de una poblacin de acuerdo con un plan de accin previamente establecido (muestreo) para obtener conclusiones.
              .row
                h5 Censo   
                p Es el estudio de todos y cada uno de los elementos de una poblacin. Esta condicin hace que este tipo de estudios no sean muy frecuentes. 
              .row
                h5 Media aritmtica    
                p Es el valor que resulta de dividir la suma de todos los valores observados entre el nmero de datos considerados.
                .row.justify-content-center 
                  .col-md-6
                    img(src='@/assets/curso/temas/tema2/form1.svg', alt='Frmula Media aritmtica')
              .row
                h5 Mediana   
                p Es el punto medio del total de observaciones, luego de que han sido ordenados y que deja al mismo nmero de observaciones por debajo de su valor, as como por arriba de l.
                .row.justify-content-center 
                  .col-md-6
                    img(src='@/assets/curso/temas/tema2/form2.png', alt='Frmula Mediana')
                p En el caso de que el total de datos sea par, la mediana va a ser dos (2) valores en vez de uno (1), el paso a seguir es sacar el promedio de esos dos datos y ese ser los resultados de la mediana. 
              .row
                h5 Moda   
                p Es el valor de la observacin o elemento que tiene la mayor frecuencia.
                .row.justify-content-center 
                  .col-md-4
                    img(src='@/assets/curso/temas/tema2/form3.png', alt='Frmula Moda')

    

    p.mb-4(data-aos="fade") Teniendo en cuenta los temas de estadstica, considere la resolucin del siguiente ejercicio autnomo.

    .tarjeta.bg-yellow.border-yellow.p-4.mb-5(data-aos="fade-down") 
