    TabsA.tema5.color-acento-botones(data-aos="fade-down") 
      .tarjeta.bg-green.p-4.p-md-5(titulo="Meteorologa")
        h5 Meteorologa
        .row
          .col-md-8
            p Es el estudio de la atmsfera y sus fenmenos  especialmente las condiciones del tiempo y del clima  y las aplicaciones prcticas de este estudio. Adems de la fsica, la qumica y la dinmica de la atmsfera, la meteorologa abarca muchos de los efectos directos de la atmsfera en la superficie de la Tierra, los ocanos y vida en general. (Organizacin Meteorolgica Mundial, 2001)
          .col-md-4
            img(src='@/assets/curso/temas/tema2/Meteorologia.png' alt='Meteorologa') 
      .tarjeta.bg-green.p-4.p-md-5(titulo="Atmosfra")
        h5 Atmosfra
        p Envoltura gaseosa que rodea la Tierra. Capa gaseosa que rodea nuestro planeta. Adems de contener el aire, incluye partculas slidas y lquidas en suspensin o aerosoles y nubes.
      .tarjeta.bg-green.p-4.p-md-5(titulo="Presin atmosfrica")
