      .tarjeta.bg-green.p-4.p-md-5(titulo="Temperatura")
        h5 Temperatura
        p Magnitud fsica que caracteriza el movimiento aleatorio media de las molculas en un cuerpo fsico. Entonces se puede decir que entre ms movimiento de las molculas mayor es la temperatura que se genera en el elemento, sustancia o cuerpo. 
        p La unidad con la que se determina la temperatura se establece en diferentes unidades dependiendo el sistema unidades empleado, algunas de las equivalencias ms utilizadas son las que aparecen en la imagen.
        .row.justify-content-center
          .col-md-8
            img(src='@/assets/curso/temas/tema2/temperatura.svg' alt='Temperatura')
      .tarjeta.bg-green.p-4.p-md-5(titulo="Velocidad del viento")
        h5 Velocidad del viento
        p Relacin entre la distancia recorrida por el aire y el tiempo que tarda en recorrerla. La velocidad instantnea o, con mayor brevedad, la velocidad, corresponde al caso de un intervalo de tiempo infinitamente pequeo. La velocidad media corresponde al caso en que el intervalo de tiempo es finito.
        p La velocidad del viento mide la componente horizontal del desplazamiento del aire en un punto y en un instante determinados. La unidad de medida es habitualmente metros por segundo (m/s).
        .row.justify-content-center
          .col-md-9
            img(src='@/assets/curso/temas/tema2/vel-viento.png' alt='Velocidad del viento') 
      .tarjeta.bg-green.p-4.p-md-5(titulo="Direccin del viento")
        h5 Direccin del viento
        p.mb-0 Direccin desde la que sopla el viento. Uno de los elementos que referencia la direccin del viento es una rosa de los vientos este grafico un smbolo en forma de crculo que tiene marcado alrededor los rumbos en que se divide la circunferencia del horizonte, y que seran sur, este, norte y oeste.
      .tarjeta.bg-green.p-4.p-md-5(titulo="Precipitacin")
        h5 Precipitacin
        p La precipitacin es la cada de partculas de agua lquida o slida que se originan en una nube, atraviesan la atmsfera y llegan al suelo. La cantidad de precipitacin es el volumen de agua lluvia que pasa a travs de una superficie en un tiempo determinado. 
        p.mb-0 Se mide en milmetros de agua, o litros cados por unidad de superficie (m), es decir, la altura de la lmina de agua recogida en una superficie plana es medida en mm o L/m.


