    p.mb-4(data-aos="fade") Con base en lo anterior, se sugiere realizar un ejercicio.

    .row.mb-5
      .col-md-9.mb-4.mb-md-0(data-aos="fade-right")
        p Si se tiene un mapa de Bogot con escala de 
          b 1/1000.000 
          | y se desea saber la distancia real entre dos puntos geogrficos A (norte) y B (Sur), al medir esta distancia en el plano corresponde a (5cm) , el proceso para calcular dicha distancia sera el siguiente:
        p 
          b.t-green2 a. 
          | Plantear la regla de 3:
          p.ps-3.mb-0 1 cm 
            b.t-green2  &gt; 
            | 1.000.000
          p.ps-3.mb-0 5 cm 
            b.t-green2  &gt; 
            | X
        p 
          b.t-green2 b.  
          | Despejar la incgnita es decir la distancia real y sustituir los datos:
        .row.ps-3.align-items-center.mb-3
          .col-2.pe-0
            p.mb-0 x =
          .col-8.col-md-3.text-center.ps-0
            .row 
              p.mb-0.sobre-linea 5 cm * 1.000.000
              p.mb-0 1 cm  
        p.ps-3 x =  5.000.000
        p 
          b.t-green2 c. 
          | Para finalizar pasamos los #[b cm] a #[b km], entonces si se sabe que un 1km equivale a 100.000cm entonces:
        .row.justify-content-center.align-items-center.mb-4
          .col-4.col-md-2.pe-0
            p.mb-0 5.000.000 <del>cm</del> *
          .col-5.col-md-2.text-center.ps-0
            .row 
              p.mb-0.sobre-linea 1 km 
              p.mb-0 100.000 <del>cm</del>
          .col-3.col-md-1.text-center.ps-0   
            p.mb-0 = 50 km
        .bg-green7.p-4 
          p.mb-0 Entonces gracias a la distancia medida sobre el mapa y la escala en el plano se logra hallar la distancia real, la cual sera de 50 Kilmetros para poder desplazarse entre el punto A y el Punto B en Bogot.
