      img(src='@/assets/curso/temas/tema2/figura.svg' alt='La imagen presenta las cinco etapas para la realizacin de una toma de muestras ya sea de ruido u olor. Estas son: 1. Identificacin del sitio de muestreo. 2. Alistamiento de los equipos, materiales e insumos para la toma. 3. Realizarcin de un plan de muestreo. 4. Toma de la muestra. 5. Preservacin y almacenamiento temporal. 6. Transporte. Desde la etapa tres se enfocan los esfuerzos tanto en la toma de muestra como en su cadena de custodia')

