    p.mb-4(data-aos="fade") Cuando se va a realizar cualquier tipo de muestreo, para la medicin de olores o ruido previamente se debe elaborar un plan que contenga la informacin y programacin relacionada con los objetivos del muestreo. A continuacin, se establecen las condiciones necesarias para la elaboracin del plan de muestreo.
    
