      .row(titulo="Consideraciones del Plan de Muestreo - Olores")
        p Para la realizacin de un estudio olfatomtrico para la medicin de olores ofensivos.
        p En esta fase supone el conocimiento previo al detalle de todos aquellos aspectos que pueden verse inmersos en la generacin de olores de una actividad o instalacin. Este conocimiento previo es fundamental para poder establecer un programa de muestreo que responda a los objetivos planteados en el estudio de impacto ambiental por olores. 
        p Esta informacin se puede obtener por medio de: 
        ul.lista-ul--color.color-secundario.fa-ul
          li.mb-0
            i.fas.fa-check
