    p.mb-4(data-aos="fade") La informacin de recopilacin general consiste en caracterizar la geografa, la poblacin y el desarrollo urbano. Se debe hacer una revisin del POT o su equivalente estudiando detalladamente aspectos relacionados con la calidad del aire. Se debe tener presente la siguiente informacin: Proximidad y el tipo de fuentes de emisiones de fondo o las concentraciones naturales; Mtodo de seguimiento utilizado; Meteorologa; Garanta de calidad; Reacciones atmosfricas; Topografa. En particular, se deber incluir una descripcin de los siguientes puntos:

    .row.fondo2-tema2-1.align-items-center.py-5.mb-5
