          p.mb-4(data-aos="fade") Ahora bien, frente a las caractersticas de un plan de muestreo se encuentran:

          .col-md-4.mb-4.mb-md-0(data-aos="fade-right")
            img(src='@/assets/curso/temas/tema2/img5.png' alt='')
          .col-md-8.mb-4.mb-md-0(data-aos="fade-left")
            AcordionA.mb-5(tipo="b" clase-tarjeta="tarjeta bg-white" data-aos="fade-down")
              .row(titulo="Revisin del esquema de procesos de la instalacin objeto de estudio")
                p La manera ms apropiada para la identificacin de los focos emisores a muestrear es mediante un esquema bsico de procesos de la instalacin en el que estn representados, a ser posible, las unidades de proceso a escala. Este esquema de procesos se debe revisar conjuntamente entre el especialista en olfatometra y el tcnico o tcnicos de la empresa objeto de estudio ya sea durante la visita a las instalaciones o por conversacin telefnica, e-mail, etc.
              .row(titulo="Identificacin de los potenciales focos emisores")
                p Sobre el esquema de procesos se identificarn todos los potenciales focos emisores diferenciando las emisiones de focos puntuales, las emisiones de fuentes difusas y las emisiones fugitivas. 
                p En el caso de fuentes puntuales, se recabar informacin de caudales aproximados de emisin. Para las fuentes de emisin de tipo difuso se solicitar el dato de las superficies implicadas. 
              .row(titulo="Procesos con emisiones discontinuas o no homogneas")
                p Es importante definir la seleccin de las siguientes variables
                ul.lista-ul--color.color-secundario.fa-ul
                  li.mb-0
                    i.fas.fa-check
                    | Dia. 
                  li.mb-0
                    i.fas.fa-check
                    | Hora.
                  li.mb-0
                    i.fas.fa-check
                    | Tiempo de muestra.
                  li
                    i.fas.fa-check
                    | # de muestras por foco.
                p El objetivo ltimo de un estudio olfatomtrico es evaluar si las emisiones de una determinada instalacin pueden ser responsables o no de quejas en la poblacin vecina. A esta conclusin se llega con la interpretacin de los valores de concentracin de inmisin, habitualmente denominados mapas de olores.
    
    
    p.mb-4(data-aos="fade") Es importante tener en cuenta tener identificados los potenciales focos emisores, y establecer la manera en la que se produce la emisin. Algunos consejos para la identificacin de las fuentes de emisin de olor son:

    .row.justify-content-center.align-items-center
      .col-md-11
        SlyderF.text-center.mb-5(columnas="col-lg-6 col-xl-4" data-aos="fade-down")
          .tarjeta.color-acento-contenido.fondo-tarjetas.p-4
            h5 Instalaciones
            p Conocer si el proceso productivo es constante a lo largo del ao o si por el contrario existen situaciones de mayor produccin en las que generalmente estarn implicadas mayores emisiones.
          .tarjeta.color-acento-contenido.fondo-tarjetas.p-4
            h5 La fuente
            p Es necesario tener en cuenta si se trata de emisiones puntuales N/A en periodos determinados o si se trata de emisiones continuas, pero con concentraciones o caudales de emisin variables. En estas situaciones habr que considerar las situaciones ms desfavorables.  
          .tarjeta.color-acento-contenido.fondo-tarjetas.p-4
            h5 Fuentes superficiales
            p Cuando la superficie emisora no es homognea hay que considerar la realizacin de distintas tomas en cada una de las zonas identificadas o la realizacin de muestreos integrados.
          .tarjeta.color-acento-contenido.fondo-tarjetas.p-4
            h5 Temperatura
            p El aumento de temperatura provoca un incremento de la presin de vapor de los compuestos voltiles responsables del olor. Esto es muy habitual en las emisiones en lminas de agua. Por este motivo los muestreos en balsas de lixiviados en vertederos o en una PTAR deberan realizarse en fechas de mayores temperaturas ambientales como primavera o verano.
          .tarjeta.color-acento-contenido.fondo-tarjetas.p-4
            h5 Nmero de muestras 
            p Esta cuestin tiene particular inters en aquellos focos de la instalacin en los que se espere los valores de emisin ms altos. Por tanto, a nivel prctico se deben proponer para el plan de muestreo rplicas en aquellas fuentes puntuales con altos caudales y/o altas concentraciones de olor.


    
    Separador 

