          p.mb-4(data-aos="fade") La toma de muestras se convierte en una de las actividades ms importantes cuando de caracterizar un contaminante se trata. Por lo tanto, se hace fundamental el conocer cmo son los procedimientos para la toma de muestras de olores y de ruido teniendo en cuenta que cada uno estos parmetros tienen caractersticas y componentes diferentes haciendo de cada metodologa una caracterstica totalmente independiente y propia de cada sector a muestrear.

          .col-md-12
            h3.subt.text-white.mb-4(data-aos="fade") Ruido

          .col-md-8.mb-4(data-aos="fade-right")
            .bloque-texto-b.bg-green9.box-shadow.p-4
              .bloque-texto-b__texto
                i.fas.fa-quote-left
                p El ruido es considerado un contaminante de tipo criterio, en otras palabras, en un factor que afecta la salud de las personas a las que se ven expuestas, cuando los decibeles estn por encima de los valores lmites permisibles por la normatividad. La percepcin del sonido depende del espectro normalizado del ruido a evaluar. En el caso de un adulto joven, en condiciones de salud normales, su rango de audicin va desde los 20 gigahercios hasta los 20000 gigahercios; esos valores de presin son ponderados y analizados en frecuencia bajo curva de ponderacin con el fin de tener un anlisis para la mayora de las personas.
                br
                h5.mb-0 - (CORPOCALDAS, 2015)
                i.fas.fa-quote-right
          .col-md-4.mb-4(data-aos="fade-left")
            img(src='@/assets/curso/temas/tema2/img6.png' alt='')

    
    p.text-center.mb-4(data-aos="fade") Se profundiza ms en la percepcin al conocer cmo funciona el odo humano.

    .row.justify-content-center.mb-5
      .col-lg-9.tarjeta.bg-gray
        ImagenInfografica.color-acento-botones.p-3.p-md-5(data-aos="fade-down")
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema2/img-infog.png', alt='Niveles de la atmsfera')

          .tarjeta.bg-yellow.p-3(x="83%" y="21%" numero="+")
            .h4.mb-2 Odo interno 
            p Es una compleja estructura interna del odo que comunica directamente con el nervio acstico. Transmite las vibraciones sonoras desde el odo medio a travs del lquido de los conductos semicirculares (rgano del equilibrio), que se unen al vestbulo, conectado con la cclea (rgano de la audicin). Consta de dos partes: el laberinto seo y el laberinto
          .tarjeta.bg-yellow.p-3(x="65%" y="75%" numero="+")
            .h4.mb-2 Odo medio
            p Est formado por la Cavidad timpnica y por el yunque, martillo y estribo, se articulan entre s y con la membrana timpnica. Las ondas sonoras se transmiten a travs de ellos cuando vibra el tmpano.
          .tarjeta.bg-yellow.p-3(x="22%" y="55%" numero="+")
            .h4.mb-2 Odo externo
            p Es la estructura externa constituida por la oreja y el conducto auditivo externo. Las ondas sonoras son conducidas a travs del odo externo hasta el odo medio.


    p.mb-4(data-aos="fade") Tal es la importancia que el Ruido tiene en la actualidad, que en Colombia la Resolucin No. 627/06 implementada por el Ministerio de Ambiente y Desarrollo Sostenible en la cual adopta la norma nacional de emisin de ruido y ruido ambiental en la que establece:

    .row.justify-content-center.mb-4
      .col-md-4.mb-4(data-aos="fade-right")
