          li.mb-0
            i.fas.fa-check
            | Los parmetros permisibles.
          li.mb-0
            i.fas.fa-check
            | Procedimientos tcnicos para la medicin de ruido.
          li
            i.fas.fa-check
            | Presentacin de informes y otras disposiciones.
        .bloque-texto-b.bg-green9.box-shadow.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h5 Segn European acstica (2017) los efectos del ruido refieren que:
            br
            br
            p El ruido generado en los grandes centros urbanos se convierte en un factor que impacta directamente en la comunidad Influye en gran importancia el trfico, la actividad industrial, comercial, entre otros quienes aportan grandes cantidades de decibeles en un sector determinado. 
            br
            br
            p La medicin de los decibeles se convierte en un factor fundamental para determinar las estrategias que se deben implementar en la reduccin del mismo, es importante conocer cules son las fuentes generadoras de este ruido debido que las actividades industriales y comerciales establecen una serie de aportes en decibeles que afectan directamente a la salud de las personas en la comunidad.
            i.fas.fa-quote-right

          

    p.mb-4(data-aos="fade") Algunas fuentes generadoras se podran clasificar de la siguiente manera:

    .row.justify-content-center.mb-4.px-md-3(data-aos="fade-down")
      .col-md-8.col-lg-6.mb-md-2.mb-lg-0.mb-4.px-md-0
        .row.px-2.pt-2.h-100
          .col-12.m-0.nav-holder2.align-items-center.px-0
            figure
              img(src="@/assets/curso/temas/tema2/tarj-hover.jpg", alt="Fuentes de ruido externas")
            .text.p-md-2.px-md-4.p-3
              .row.px-1.pt-1.mt-xs-3.mt-md-3.pt-md-0.mt-lg-2.mt-xl-3
                h5.mt-1.mt-lg-3.mb-3 Fuentes de ruido externas
                p El ruido debido al trfico rodado, al paso de trenes, al trfico areo, al trfico martimo, a las obras pblicas, a las actividades industriales, a las actividades urbanas comunitarias y a los agentes atmosfricos.
      .col-md-8.col-lg-6.mb-md-2.mb-lg-0.mb-4.px-md-0
        .row.px-2.pt-2.h-100
          .col-12.m-0.nav-holder2.align-items-center.px-0(@mouseover="mostrarIndicador = false")
