            .text.p-md-2.px-md-4.p-3
              .row.px-1.pt-1.mt-xs-3.mt-md-3.pt-md-0.mt-lg-2.mt-xl-3
                h5.mt-1.mt-lg-3.mb-3 Fuentes de ruido internas
                p El ruido debido a las personas, a los impactos, a los aparatos de radio y televisin, a los aparatos electrodomsticos, a las instalaciones de fontanera y calefaccin, a los ascensores, a las instalaciones de ventilacin y climatizacin, a las instalaciones elctricas y a otras fuentes (como animales).

    p.mb-4(data-aos="fade") Los efectos del ruido en la salud dependen especficamente de la exposicin prolongada al ruido ya sea en el da a da o en el puesto de trabajo este puede causar algunos problemas como la hipertensin, enfermedades cardiacas tambin puede afectar, aunque no lo creamos, la lectura, la atencin y la memoria.

    .row.justify-content-center.align-items-center.mb-4
      .col-md-8.mb-4(data-aos="fade-right")
        .bloque-texto-b.bg-green9.box-shadow.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            h5.mb-1.mt-3 Segn CORPOCALDAS (2015)
            br
            br
            p Los fallos en el desempeo de la actividad laboral pueden producir accidentes el ruido con decibeles por encima de los 80 db y aumentar el comportamiento agresivo y establecer ciertos problemas mentales debido a la demanda de tranquilizantes y somnferos por la ansiedad y angustia que este puede provocar (p.10).
            i.fas.fa-quote-right
            
      .col-md-4.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img8.png' alt='')


    .row.justify-content-center.mb-4
      .col-md-4.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img9.png' alt='')
      .col-md-8.mb-4(data-aos="fade-left")
        p El ruido puede causar otros muchos problemas, pero la principal consecuencia social es el deterioro de la audicin, que produce incapacidad de entender una conversacin en condiciones normales y que est considerado una desventaja social severa. Los efectos ms significativos estn los siguientes:
        ul.lista-ul--color.color-secundario.ps-3.ps-md-4.fa-ul
