          li.mb-0
            i.fas.fa-check
            | Disminucin de la capacidad auditiva.
          li.mb-0
            i.fas.fa-check
            | Perturbacin del sueo y descanso.
          li.mb-0
            i.fas.fa-check
            | Estrs.
          li.mb-0
            i.fas.fa-check
            | Fatiga, neurosis, depresin.
          li.mb-0
            i.fas.fa-check
            | Zumbidos y tinnitus, en forma continua intermitente.
          li.mb-0
            i.fas.fa-check
            | Efectos sobre el rendimiento.
          li.mb-0
            i.fas.fa-check
            | Alteracin del sistema circulatorio.
          li.mb-0
            i.fas.fa-check
            | Alteracin del sistema digestivo.
          li.mb-0
            i.fas.fa-check
            | Aumento de secreciones hormonales (tiroides y suprarrenales).
          li.mb-0
            i.fas.fa-check
            | Trastornos en el sistema neurosensorial.

    .row.justify-content-center.align-items-center.mb-4
      .col-md-8.mb-4(data-aos="fade-right")
        .bg-green7.p-4.mb-4
          p.mb-0 El ruido tiene un componente subjetivo, puesto que cualquier sonido puede ser considerado molesto para algunas personas y para otras de pronto no, esto Incide directamente en el receptor del ruido en donde tambin influye el horario en el que se produce, el tiempo exposicin y los antecedentes socioculturales del sector con respecto a una determinada actividad que produzca el ruido.
        p Es importante diferenciar que la medicin del ruido que se va a realizar debe establecer un alcance, puesto que el Ruido Ambiental se mide teniendo en cuenta un rea determinada por su impacto (Parques, Zonas Residenciales, Hospitales, entre otros), en cambio la medicin por Emisin de Ruido se mide siempre y cuando est relacionada con la generacin de ruido que puede generar una empresa o proyecto.
      .col-md-4.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img10.png' alt='')

    p.mb-4(data-aos="fade") Para este caso la medicin por emisin de ruido se aplica directamente a las fuentes industriales dicha medicin se debe establecer una distancia de 1.5 metros a partir de la fachada o permetro especfico del rea a medir en este caso vamos a ver como es el procedimiento para hacer la medicin por emisin de ruido, tal como lo presenta el grfico.

    .row.justify-content-center(data-aos="fade-down")
      .col-lg-9
        .titulo-sexto.color-acento-contenido.mb-2
          p.mb-0 #[b Figura 1] Medicin del ruido 
      .col-lg-6.px-md-0.mb-4
        figure
          img(src='@/assets/curso/temas/tema2/fig1.svg' alt='Medicin del ruido')
      .col-lg-3.px-md-0.mb-4
        .bg-green7.p-4.p-md-5.h-100
          p.mb-0 La medicin de la emisin de ruido se realiza a 1.5 m de la fachada de una edificacin y a 1.20 m a partir del nivel mnimo donde se encuentre instalada la fuente de emisin de ruido.
    
    .fondo2-tema2-2.align-items-center.py-5.mb-5
      .col-lg-12.col-12.px-lg-5.px-4
