                      h3.mb-1 Consideraciones previas al muestreo
                      p.mb-0 Se debe tener en cuenta que antes del muestreo se deben realizar algunas actividades previas con el fin de verificar el equipo de medicin (Sonmetro) y revisar tanto el plan de muestreo como las actividades a realizar. Se presentan algunas consideraciones.
                    .col-sm-auto
                      a.boton.color-acento-botones(:href="obtenerLink('/downloads/Consideraciones_previas_muestreo.pdf')" target="_blank")
                        span Descargar
                        i.fas.fa-file-download
    
          .row.justify-content-center
            .col-md-4.mb-4(data-aos="fade-right")
              img(src='@/assets/curso/temas/tema2/img11.png' alt='')
            .col-md-8.mb-4(data-aos="fade-left")
              p Ahora bien, luego de las condiciones previamente descritas, se procede a realizar las mediciones. El equipo debe estar colocado fijamente sobre un trpode a una altura de 1.2 mts a partir del nivel mnimo donde se encuentre la fuente instalada a una distancia de 1.5 mts de la fachada o cerramiento perimetral de la empresa, proyecto o fuente emisora de ruido.
              p Con el fin de prevenir posibles errores durante la medicin el sonmetro debe estar orientado con la pantalla anti-viento hacia la direccin de la fuente especfica, segn sea el caso nicamente debe estar el tcnico que realiza las mediciones, entre ms personas que estn presentes en el sitio de medicin y ms an cerca al sonmetro, se podra presentar fenmenos como el apantallamiento, que influye negativamente en la recepcin de la informacin por parte del sonmetro.

          p.mb-4(data-aos="fade") La separacin que debe haber entre el tcnico (y las dems personas, si las hubiera en el momento de la medicin) y el sonmetro, debe ser de por lo menos 0.5 m. (Universidad de Medelln, 2011), tal como se presenta en el siguiente grfico.
          
          .col-lg-5
            .titulo-sexto.color-acento-contenido.mb-2
              p.mb-0 #[b Figura 2] Medicin en campo 
