    p.mb-4(data-aos="fade") Es importante cuando se realice la medicin en el da o en la noche tener presente que las condiciones en donde el proyecto o la empresa est funcionando con la mayor intensidad de ruido con el fin de medir en las condiciones reales de operacin de la fuente.

