      .col-lg-4.col-8.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img12.png' alt='')
      .col-lg-8.mb-4(data-aos="fade-left")
        p Tambin, se deben realizar por punto, dos procesos de medicin de una hora de captura de informacin o a intervalos de 15 minutos (Una medicin con la fuente de emisin operando en su mximo nivel de produccin o de emisin de ruido y una Medicin (con la fuente de emisin en condiciones neutras de operacin o mejor dicho sin la emisin de ruido generado). Todo esto, con el fin de determinar el ruido residual.
        .bg-degrade-green.tarjeta.p-4
          h3.text-white Glosario  Parmetros de medicin de ruido 
          p.text-white En caso de presentarse ruidos que no pertenezca a la operacin directa de la fuente, como pitos, altavoces, sirenas, perifoneos, etc., estos no debern ser tenidos en cuenta y la informacin debe ser relacionada en el informe tcnico. A continuacin se describen los principales parmetros empleados para la medicin de la emisin de ruido, de acuerdo con la Resolucin 627 de 2006.
          .row 
            .col-md-7
              a.anexo.mb-3(:href="obtenerLink('/downloads/Parametros_medicion_ruido.pdf')" target="_blank")
                .anexo__icono
                  img(src="@/assets/template/icono-pdf.svg")
                .anexo__texto
                  p #[b Anexo.] Parmetros de medicin de ruido

    .row.justify-content-center.align-items-center
      .col-md-8.mb-4(data-aos="fade-right")
        p Teniendo en cuenta la informacin anterior, se puede indicar que los principales parmetros para la medicin de la emisin de ruido son:
        ul.lista-ul--color.color-secundario.ps-3.ps-md-4.fa-ul
          li.mb-0
            i.fas.fa-check
            | Nivel de presin sonora continuo equivalente con filtro de ponderacin A, LAeq,T y respuesta lenta (S).
          li.mb-0
            i.fas.fa-check
            | Ruido residual, medido como nivel de presin sonora continuo equivalente con filtro de ponderacin A, LAeq,T, Residual y respuesta lenta (S).
          li.mb-0
            i.fas.fa-check
            | Nivel percentil L90.
        .bloque-texto-b.bg-green9.box-shadow.p-4.mb-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            p Si por alguna razn no es posible medir el ruido residual, se toma como valor indicativo (o representativo) de ste el correspondiente al nivel percentil L90. Esto se debe hacer constar en el informe tcnico, en el que a su vez se deben especificar las razones por las cuales no fue posible medir el ruido residual. 
            br
            br
            h5.mb-0 - (Universidad de Medelln, 2011) 
            i.fas.fa-quote-right

         
        p Cuando se va a realizar la medicin se debe establecer unos tiempos o intervalos que permitan tomar muestras caractersticas representativas de acuerdo con lo establecido en la Resolucin 627de 2006 el tiempo mximo es de una (1) hora de captura informacin con 15 minutos como mnimo con el fin de evitar incurrir en mediciones cortas que generaran baja representatividad. Si las condiciones para medir en esos tiempos no se dan la duracin de cada intervalo de tiempo de medicin no podr ser inferior a 5 minutos.     
      .col-md-4.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img13.png' alt='')


    p.mb-4(data-aos="fade") Es tiempo de profundizar en los clculos.
    
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-green6" data-aos="fade-down")
      .row(titulo="Nivel promedio")
        p Hay que obtener un solo valor con las mediciones de cada intervalo de tiempo, si es que las mediciones se hicieron por intervalos, para el LAeq,1h, LAeq,1h, Residual (o nivel percentil 90) para las mediciones con ponderacin temporal lenta (S), de acuerdo con la siguiente ecuacin:
        p En la cual: 
        .row.justify-content-center.mb-4
          .col-lg-3.col-md-6
            img(src='@/assets/curso/temas/tema2/acord-form1.png', alt='Frmula')
        p.mb-0 #[b LAeq,1h] = Nivel de presin sonora continuo equivalente promedio, dB(A). 
        p.mb-0 #[b LAeq,Ti] = Nivel de presin sonora continuo equivalente de cada evento, dB(A). 
        p #[b Ti]= Intervalo de tiempo del evento individual, min. 
        p Ahora, si los intervalos de tiempo fueron iguales, la ecuacin anterior se puede simplificar de la siguiente manera: 
        .row.justify-content-center.mb-4
          .col-lg-3.col-md-6
            img(src='@/assets/curso/temas/tema2/acord-form2.png', alt='Frmula')
        p.mb-0 En la cual: 
        p #[b N ] = Nmero de mediciones.
      .row(titulo="Clculo emisin o Aporte de ruido")
        p Al obtener los registros de los decibeles producto de la medicin de ruido durante el monitoreo, el clculo de la emisin o aporte de ruido de la fuente se obtiene al restar logartmicamente el nivel corregido de presin sonora continuo equivalente total con filtro de ponderacin (A) del nivel corregido de presin sonora continuo equivalente residual con filtro de ponderacin A, del cmo se expresa a continuacin.
        .row.justify-content-center.mb-4
          .col-lg-5.col-md-9
            img(src='@/assets/curso/temas/tema2/acord-form3.png', alt='Frmula')
        p Donde: 
        p.mb-0 #[b Leq emisin] = Nivel de emisin de ruido o aporte de la(s) fuente(s) de emisin de ruido con filtro de ponderacin A, dB(A). 
        p.mb-0 #[b LRAeq,1h] = Nivel corregido de presin sonora continuo equivalente total con filtro de ponderacin A, dB(A). 
        p #[b LRAeq,1h, Residual] = Nivel de ruido residual corregido con filtro de ponderacin A (medido como nivel de presin sonora continuo equivalente o como nivel percentil 90), dB(A).
        p El nivel de emisin de ruido, o aporte de la(s) fuente(s) de emisin de ruido, con filtro de ponderacin A (Leq emisin) es el que se compara con la tabla 1 del artculo 9 (Estndares mximos permisibles de emisin de ruido) de la Resolucin 627 del 7 de abril de 2006 del Ministerio de Ambiente, o aquella que la sustituya, modifique o complemente. 
        p Es importante tener presente que el punto donde se realiz la medicin debe ser localizado, para posteriormente reconocerlo e identificarlo y as generar las respectivas observaciones que se deriven de los anlisis que se concluyan posterior a la medicin.

    .row.justify-content-center.mb-4
      .col-md-3.col-9.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img14.png' alt='')
      .col-md-9.mb-4(data-aos="fade-left")
        .bg-primario.tarjeta.mb-4(data-aos="fade-down")
          .row.p-4.pt-0.p-md-0
            .col-md-2.col-5
              img.ps-md-4.pt-0(src='@/assets/curso/temas/tema2/importante.png' alt='')
            .col-md-10.py-4.text-white.pe-md-5
              h5 Para tener en cuenta!
              p Si la diferencia aritmtica entre LRaeq,1h y LRaeq,1h, Residual es igual o inferior a 3 dB(A), se deber indicar que el nivel de emisin de ruido es del orden igual o inferior al ruido residual (LRaeq,1h, Residual). En este caso se considera que el nivel de ruido residual es demasiado alto para establecer claramente cul es la contribucin de la fuente. 
              p.mb-0 Esto no implica que la fuente de emisin de ruido objeto de estudio est exenta del cumplimento de la norma, sino que se debe realizar un estudio exhaustivo de la fuente y su entorno, debido a la incertidumbre tan alta de la medicin, para poder determinar apropiadamente la emisin de ruido.
        p Cuando se finaliza el muestreo y una vez recogido los datos por punto, teniendo en cuenta que deben cuantificar una hora por punto medicin, se debe realizar una calibracin final empleando el pistfono; esto permite verificar que la medicin cumpla con las condiciones de fiabilidad presentes durante la medicin.
        p Los datos recolectados se deben registrar en las hojas de campo y adicional bajar los datos con el software integrado en el sonmetro a una hoja de clculo, dando as la confirmacin de los datos tanto en fsico como en digital para posteriormente realizar los respectivos informes de campo.
        p Tambin es importante que durante la medicin se debe generar registro fotogrfico por cada punto de medicin y registrar los datos de la medicin del viento en el muestreo que se llev al lugar.


    p.mb-4(data-aos="fade") Finalmente, se cierra esta temtica de medicin de ruido presentando una ruta de medicin tomada del Protocolo para la medicin de emisin de ruido, ruido ambiental y realizacin de mapas de ruido de la Universidad de Medelln (2011, p.33-35):

    .row.justify-content-center.align-items-center.mb-4
      .col-md-8.mb-4(data-aos="fade-right")
        LineaTiempoD.color-secundario
          p(numero="1" titulo="Condiciones meteorolgicas") Antes de emprender una medicin para determinar la emisin de ruido, es necesario verificar las condiciones meteorolgicas. (verificar la velocidad del viento no debe ser mayor a 3m/s).
          p(numero="2" titulo="Ubicacin del sonmetro") El sonmetro se ubica con el micrfono ubicado haca la fuente a 1.5 de la fachada y a 1.20 de altura, separado del operador del equipo a 50 cm para evitar el apantallamiento del sonido. 
          p(numero="3" titulo="Calibracin") Realizar calibracin acstica del sonmetro con el pistfono antes de iniciar la medicin.
          p(numero="4" titulo="Condiciones de medicin") Las mediciones se realizan con respuesta lenta (S) y con filtros de ponderacin frecuencial A y Z (antes lineal) y con respuesta por impulso (I) y con filtro de ponderacin frecuencial A. La medicin deber realizarse en el da, horario y condiciones de funcionamiento donde la intensidad de la emisin de ruido sea mayor. 
          p(numero="5" titulo="Proceso de medicin") Se deben realizar dos (2) procesos de medicin de por lo menos quince (15) minutos de captura de informacin cada uno; uno con la(s) fuente(s) de emisin de ruido funcionando durante el perodo de tiempo de mayor emisin o incidencia para determinar el nivel de presin sonora continua (LAeq,1h) y otro sin la(s) fuente(s) funcionando, para determinar el ruido residual, (LRaeq,1h,Residual) 
          p(numero="6" titulo="Ruido residual") El ruido residual (nivel de presin sonora continuo equivalente con filtro de ponderacin A, LRaeq,1h, Residual) se mide con la(s) fuente(s) especfica(s) ap.ada(s) y en el mismo sitio de la medicin anterior, manteniendo invariables los condicionantes del entorno y durante el tiempo y forma.
          p(numero="7" titulo="Nmero de mediciones") El nmero mnimo de mediciones a ejecutar es uno (1), el cual consta de dos (2) procesos de medicin de por lo menos quince (15) minutos de captura de informacin cada uno, uno con la(s) fuente(s) de emisin de ruido funcionando y otro con la(s) fuente(s) especfica(s) ap.ada(s), en intervalos de tiempo distribuidos uniformemente en una hora, en el horario diurno o nocturno requerido. 
          p(numero="8" titulo="Interrupciones") Si durante las mediciones se presentan ruidos ocasionales que no pertenecen a la fuente objeto de evaluacin, como por ejemplo sirenas, altavoces, etc., stos no se deben tener en cuenta. 
          p(numero="9" titulo="Subsector restrictivo") Cuando la emisin de ruido en un sector o subsector trascienda a sectores o subsectores vecinos o inmersos en los estndares mximos permisibles de emisin de ruido son aquellos que corresponden al sector o subsector ms restrictivo. 
      .col-md-4.col-9.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img15.png' alt='')

    .row.fondo3-tema2-2.align-items-center.py-5.pb-4.mb-4
      .col-lg-12.col-12.px-lg-5.px-4
        .row.justify-content-center.align-items-center      

          .col-md-12
            h3.subt.text-white.mb-4(data-aos="fade") Olores
            p Los olores ofensivos se convierten en un problema medioambiental dependiendo el origen y el tipo de fuente que la generan, de esta manera da origen de numerosas quejas que con fundamento produce molestias a la poblacin en determinada rea de influencia. 
            p.mb-4(data-aos="fade") An en el caso de que las sustancias olorosas emitidas no posean ningn efecto perjudicial para la salud, las molestias causadas por olores pueden constituir un serio problema que necesita ser evaluado, investigado en sus causas y solucionado para responder a las quejas de la sociedad.
          
          .col-md-4.mb-4(data-aos="fade-right")
            img(src='@/assets/curso/temas/tema2/img16.png' alt='')
          .col-md-8.mb-4(data-aos="fade-left")
            p En esta seccin se desarrollarn los conceptos generales de los olores ofensivos medicin del contaminante y la normatividad asociada para la vigilancia y control de este parmetro contaminante de acuerdo con lo establecido en la Resolucin 1541 de 2013 en Colombia
            .bg-blue3.p-4
              p.mb-0 El olfato es uno de los sentidos ms importantes que tiene el ser humano y es el encargado de detectar y procesar los olores que estn presentes en el aire. Todos estos olores penetran por la nariz, especficamente acceden por sus fosas nasales y llegan a la cavidad nasal, donde las clulas receptoras al detectar este olor los transmite al bulbo olfatorio como impulsos, los cuales, a su vez, se encargan de enviar seales al cerebro para que interprete esta sensacin en un olor.

    
    p.mb-4(data-aos="fade") Para entender cmo funciona el sentido del olfato se expone su estructura y las partes principales, segn el portal web Partesdel.com (2017).

    .row.justify-content-center.mb-5
      .col-lg-9.tarjeta.bg-gray
        ImagenInfografica.color-acento-botones.p-3.p-md-5(data-aos="fade-down")
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema2/img-infog2.png', alt='Niveles de la atmsfera')

          .tarjeta.bg-yellow.p-3(x="38.5%" y="13%" numero="+")
            .h4.mb-2 Bulbo olfativo 
            p Parte del cerebro donde todas seales sensoriales son interpretadas. Refiere a la parte interior de la nariz, la cual est cubierta por una pared compuesta de mucosa, encargada de producir el moco. Esta se encarga de mantener la humedad en la cavidad nasal para as poder impedir la entrada de suciedad, polvo y de ciertas partculas que pueden causar dao si acceden a los pulmones.
          .tarjeta.bg-yellow.p-3(x="22%" y="24%" numero="+")
            .h4.mb-2 Nervio olfativo 
            p Localizados en el interior de la cavidad craneana donde cada nervio al ensancharse crea un bulbo olfatorio. Este nervio es totalmente sensitivo, es precisamente quien se encarga de transmitir los impulsos olfatorios desde la nariz hasta el sistema nervioso central.
          .tarjeta.bg-yellow.p-3(x="21%" y="46%" numero="+")
            .h4.mb-2 Pituitaria 
            p Se encarga de calentar todo el aire que se respira. En esta zona donde estn las clulas olfativas, donde las sustancias odorferas las impresionan. Esta presenta un tono amarillento y se halla en el cornete superior, o sea, en el espacio donde las ramas del nervio olfatorio se distribuyen.
          .tarjeta.bg-yellow.p-3(x="59%" y="87%" numero="+")
            .h4.mb-2 Cilios olfatorios 
            p Se trata de clulas nerviosas que contienen pelos muy pequeos y visibles solo con un microscopio, las cuales tienen la funcin de acumular todos los olores para que as los enve el receptor hacia el encfalo, lo cual lo realiza en forma de impulso nervioso.
          .tarjeta.bg-yellow.p-3(x="82%" y="73%" numero="+")
            .h4.mb-2 Narinas 
            p Se trata de dos cavidades que se mantienen separadas a travs de un tabique. Se ubican en la cabeza, justo sobre la boca.
          .tarjeta.bg-yellow.p-3(x="92%" y="49%" numero="+")
            .h4.mb-2 Fosas nasales 
            p Se trata de las dos cavidades que se encuentran sobre la boca.
          .tarjeta.bg-yellow.p-3(x="82%" y="37%" numero="+")
            .h4.mb-2 Nariz 
            p Posee dos compartimientos que se encuentran divididos por el tabique nasal, donde se muestran dos orificios de salida que se le conoce como narinas. En su otro lado este rgano finaliza en unas aberturas que tienen conexin directa con la faringe. Cada fosa nasal posee en sus paredes laterales unos huesos esponjosos que se le conoce como cornetes. Bajo estos existe un espacio que mantiene la conexin de la nariz y los senos paranasales, lo cual se conoce como meatos.
          .tarjeta.bg-yellow.p-3(x="87%" y="24%" numero="+")
            .h4.mb-2 Receptores olfativos 
            p Estos se hallan en la parte superior de las fosas nasales. Aqu es donde el cornete superior es cubierto por la pituitaria amarilla y se crea la conexin con el bulbo olfatorio, estos tienen como funcin detectar los olores.

    .row.justify-content-center.mb-4
      .col-lg-3.col-5.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img17.png' alt='')
      .col-lg-9.mb-4(data-aos="fade-left")
        .tarjeta.bg-degrade-green.p-3.mb-4
          .row.justify-content-around.align-items-center
            .col-3.col-sm-2.col-lg-2
              img.px-lg-3(src="@/assets/curso/temas/tema2/pdf.svg")
            .col
              .row.justify-content-between.align-items-center
                .col.mb-3.mb-sm-0.text-white
                  h3.mb-1 Glosario - Conceptos de medicin de olores 
                  p.mb-0 Todos los olores son potencialmente ofensivos y que dependen especficamente de: frecuencia, intensidad, duracin, ofensividad y localizacin. Ahora se amplan los conceptos y metodologas que se van a profundizar, por tanto se hace necesario incorporar las siguientes definiciones.
                .col-sm-auto
                  a.boton.color-acento-botones(:href="obtenerLink('/downloads/Conceptos_medicion_olores.pdf')" target="_blank")
                    span Descargar
                    i.fas.fa-file-download
        p Tal es la importancia que el control y seguimiento tiene en la actualidad los olores ofensivos, que en Colombia se implement la Resolucin No. 1541/2013 por el Ministerio de Ambiente y Desarrollo Sostenible, por la cual se establecen los lmites permisibles de calidad del aire o de inmisin como el procedimiento para la evaluacin de actividades que generan olores ofensivos y se dictan otras disposiciones.
        p En esta norma encontraremos:
        ul.lista-ul--color.color-secundario.fa-ul
          li.mb-0
            i.fas.fa-check
            | La reglamentacin para la recepcin de quejas.
          li.mb-0
            i.fas.fa-check
            | Niveles permisibles de inmisin de olores ofensivo.
          li.mb-0
            i.fas.fa-check
            | Presentacin del PRIO plan para la reduccin del impacto por olores ofensivos.

    p.mb-4(data-aos="fade") La percepcin de los olores y las molestias que ellos generan dependen del sitio y la ubicacin de las diversas fuentes que pueden ser considerados generadores olores, como criaderos de animales, plantas de tratamiento de aguas residuales, rellenos sanitarios entre otras. Con respecto al origen de los olores, en los diversos procesos, se podran clasificar en dos tipos:
    
    ul.lista-ul--color.color-secundario.ps-3.ps-md-4.fa-ul
      li.mb-0
        i.fas.fa-check
        span #[b Fuentes puntuales:] las chimeneas, conductos, salidas de ventilacin.
      li.mb-0
        i.fas.fa-check
        span #[b Fuentes difusas:] generalmente son superficies solidas o liquidas, tales como pozos de secado de lodos, plantas de compostaje, vertederos, piscinas, biofiltros, entre otros. 

    .row.justify-content-center.mb-4
      .col-md-9.mb-4(data-aos="fade-right")
        .bg-primario.tarjeta.mb-4
          .row.p-4.pt-0.p-md-0
            .col-md-2.col-4
              img.ps-md-4.pt-0(src='@/assets/curso/temas/tema2/olores.svg' alt='')
            .col-md-10.py-4.text-white.pe-md-5
              h5 Olores ofensivos
              p Es importante no solamente conocer las actividades de generan los olores sino tambin, identificar cules son las sustancias que lo producen, estas sustancias se pueden encontrar en la corriente de aire en forma gaseosa o lquida, tambin en los recursos hdricos y en el suelo. Se sugiere revisar la tabla de sustancias de olores ofensivos por actividad que se encuentra en la Resolucin 1541 de 2013; recurso que se encuentra en el material de apoyo. 
              p.mb-0 A nivel internacional se han clasificado actividades como las principales generadoras de olores ofensivos y por esta razn se explica particularmente su incidencia en la generacin de olores ofensivos, que se visualizan en la tabla de aspectos ambientales de las sustancias asociadas a olores ofensivos en el Lineamiento de Vigilancia Ambiental, 2012, p.24; recurso que se encuentra en el material de apoyo. 
      .col-md-3.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img18.png' alt='')
        

    p.mb-4(data-aos="fade") Se muestran a continuacin algunas fuentes generadoras de olores.

    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg-green6" data-aos="fade-down")
      .row.px-md-2(titulo="Plantas de tratamiento de agua residual")
        .col-md-7
          p Las plantas de tratamiento de agua residual (PTAR) que tenga fallas en su diseo u operacin, sea de tipo fsico, qumico o biolgico, puede generar malos olores. El medio anaerobio es el ms susceptible a esta situacin, debido al metabolismo de las bacterias sulfato-reductoras, especialmente si en el agua residual existen altas concentraciones de sulfatos y sulfuros. (Ministerio de Salud, 2012 p. 24)
        .col-md-5
          img(src='@/assets/curso/temas/tema2/acord-img1.png' alt='')
      .row.px-md-2(titulo="Rellenos sanitarios")
        .col-md-7
          p Desde la aparicin de las ciudades han existido problemas de salud pblica por la generacin de residuos, Los olores ofensivos provenientes de los rellenos sanitarios generalmente ocurren por un mal funcionamiento de los biofiltros o prcticas inadecuadas, como reutilizacin de reas previamente utilizadas o por descargas incontroladas de residuos. Esta percepcin de los malos olores no es permanente, sino que su presencia est asociada a las condiciones del clima y a la direccin de los vientos, siendo las horas nocturnas y los das clidos que siguen a las lluvias, cuando los olores son ms intensos. (Ministerio de Salud, 2012 p.28)
        .col-md-5
          img(src='@/assets/curso/temas/tema2/acord-img2.png' alt='')
      .row.px-md-2(titulo="Curtido de cuero")
        .col-md-7
          p Las industrias de curtido y acabado de cueros, tradicionalmente han aparecido ante el mundo como algunas de las ms sucias y contaminantes debido al proceso para que este producto tenga el terminado que conocemos comercialmente. Como consecuencia, el agua residual de la industria de curtido tiene altas concentraciones de materia orgnica, compuestos de nitrgeno, sulfuros, slidos suspendidos, compuestos de cromo y pH elevado. 
          p La alta carga de materia orgnica provoca la creacin de condiciones anaerobias en cuerpos o cursos de agua, debido al elevado consumo de oxgeno disuelto. Estas condiciones, adems de afectar la vida acutica, favorecen la produccin de algunos gases nocivos como cido sulfhdrico y metano, indicadores cualitativos para la generacion de olores ofensivos de muy mal carcter. (Ministerio de Salud, 2012 p. 30)
          p Generalmente los malos olores provienen de un inadecuado control en las diferentes actividades, las mas significativas son: el pelambre, descarne, la limpieza de equipos y recipientes y el vertimiento de efluentes lquidos y slidos a los cursos de agua.
        .col-md-5
          img(src='@/assets/curso/temas/tema2/acord-img3.png' alt='')
      .row.px-md-2(titulo="Industria petroqumica")
        .col-md-7
          p En la industria petrolera, los yacimientos de crudo contienen contaminantes entre los cuales se encuentra el azufre, el cual al combinarse con molculas de hidrogeno forma el cido sulfhdrico, que es liberado de los hidrocarburos en forma de gas al suministrarles calor. Existen microorganismos anaerobios capaces de utilizar compuestos de azufre, tales como las bacterias sulfato reductoras (BSR) y las bacterias tiosulfato reductoras (BSTR), las cuales utilizan el azufre como aceptores finales de electrones en la oxidacin de la materia orgnica, produciendo principalmente cido sulfhdrico y mercaptanos. (Ministerio de Salud, 2012 p.30).
          p Otro aspecto importante que tiene que ver con la industria petrolera y la contaminacin por olores ofensivos son los derrames de petrleo debido a la ocurrencia de accidentes o atentados a la infraestructura petrolera en nuestro pais influyendo en la salud de las poblaciones afectadas.
        .col-md-5
          img(src='@/assets/curso/temas/tema2/acord-img4.png' alt='')


    p.mb-4(data-aos="fade") Un olor que en principio es agradable cuando es percibido con tanta intensidad puede convertirse en molesto, es por eso que el concepto de olor ofensivo tambin se vuelve muy subjetivo, debido a que los olores dependen especficamente de la intensidad o fuerza y de la concentracin en aire del compuesto(s) que lo origina.
    
    .row.justify-content-center.mb-2
      .col-md-5.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img19.png' alt='')
      .col-md-7.mb-4(data-aos="fade-left")
        .bg-blue3.p-4.mb-4
          p As como el ruido, las vibraciones y la luz, el olor es considerado un estresor ambiental. Segn la Organizacin Mundial de la Salud (OMS) La salud es un estado de completo bienestar fsico, mental y social, y no solamente la ausencia de afecciones o enfermedades. As la molestia por olor afecta la calidad de vida de las personas.
          p.mb-0 Algunos estudios sealan que los olores pueden afectar al estado psquico de las personas, influyendo negativamente sobre su estado anmico y pudiendo provocar situaciones de estrs, por esta razn, el olor es considerado un contaminante. (Ministerio del Medio Ambiente de Chile, s.f.)
        p Algunos sntomas negativos provocados por la molestia de olor son: mareos, nuseas, vmitos, dolor de cabeza, problemas de concentracin, otros. Algunos de estos efectos dependen de la dosis y pueden aumentar con el tiempo.
        p La OMS seala que algunas sustancias tienen propiedades malolientes en concentraciones muy por debajo de aquellas en las que ocurren efectos txicos. As para la evaluacin de efectos sensoriales se han identificado tres umbrales: 
          b deteccin, reconocimiento y molestia.

    p.mb-4(data-aos="fade") Para la medicin de olores ofensivos la resolucin 627 del ao 2006 establece como referencia el protocolo para la medicin de olores ofensivos en el cual establecen los procedimientos y caracterizacin de olores, en esta seccin veremos las condiciones generales de la medicin en campo en olores ofensivos. Para caracterizar los olores ofensivos se pueden aplicar dos tcnicas: las sensoriales y las analticas.

    .row.mb-5
      .col-md-6.bg-green.tarj-btn.p-md-5.p-4.mb-4.mb-md-0
        .h-100
          img.mb-md-5.mb-4(src='@/assets/curso/temas/tema2/ico4.svg' alt='' style="width: 65px;")
          h5 Las tcnicas sensoriales 
          p Se basan en la percepcin de los olores por el olfato humano. Tambin incluyen la determinacin del carcter de un olor (mapeo) y el nivel de agrado o desagrado de un olor (tono hednico).
      .col-md-6.bg-green10.tarj-btn.p-md-5.p-4.mb-4.mb-md-0
        .h-100
          img.mb-md-5.mb-4(src='@/assets/curso/temas/tema2/ico5.svg' alt='' style="width: 65px;")
          h5 Las tcnicas analticas 
          p Para caracterizacin de olores son relativamente fciles de aplicar e identifican cuantitativamente las especies qumicas presentes en un olor. La desventaja de la tcnica es que no provee informacin alguna referente a la molestia que puede generar un olor. Por esta razn, las tcnicas analticas son usadas para definir criterios de diseo de mtodos de control de olores.
    
    
    p.mb-4(data-aos="fade") Las siguientes tcnicas son las ms empleadas en las mediciones de olores ofensivos, de acuerdo con el Ministerio del MinSalud, 2012, pp. 33-35).

    .row.justify-content-center.mb-5(data-aos="fade-down")
      .col-lg-7
        ImagenInfografica.color-acento-botones
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema2/img-ifog3.svg', alt='Olfatometra dinmica')

          .tarjeta.bg-yellow.p-3(x="51%" y="6%" numero="+")
            .h5.mb-2 Olfatometra dinmica
            p.mb-0 En esta tcnica se emplea el olfato humano como medio de captura y sensor de olor de esta manera se establece una metodologa para la determinacin del olor de una muestra gaseosa usando un panel panelistas como sensores para posteriormente determinar la emisin. El objetivo primordial de un estudio olfatomtrico es evaluar si las emisiones de una determinada instalacin pueden ser responsables o no de quejas en la poblacin vecina. A esta conclusin se llega con la interpretacin de los valores de concentracin de inmisin, habitualmente denominados mapas de olores.
          .tarjeta.bg-yellow.p-3(x="33%" y="57%" numero="+")
            .h5.mb-2 Nomogramas
            p.mb-0 Los nomogramas son instrumentos grficos bidimensionales que permiten el clculo aproximado de una funcin. Los ms sencillos contienen dos ejes. En el eje horizontal se coloca la distancia a la que se estima que existe una determinada concentracin o a la que se esperara que haya molestias por olores y en el eje vertical se introduce el valor de emisin del olor (calculado partiendo de datos reales o estimados).
          .tarjeta.bg-yellow.p-3(x="42%" y="75%" numero="+")
            .h5.mb-2 Mediciones de campo
            p.mb-0 Esta metodologa se basa en la observacin y deteccin del olor en campo, mediante observadores que se sitan en diferentes puntos de la zona en la que se quiere realizar la evaluacin de la molestia por olores. (La norma alemana VDI 3940 es la base de esta evaluacin y permite obtener la frecuencia de deteccin del olor)
          .tarjeta.bg-yellow.p-3(x="33%" y="94%" numero="+")
            .h5.mb-2 Tablas FIDO
            p.mb-0 Esta herramienta se basa en el estudio de cuatro parmetros: la frecuencia, la duracin de la deteccin, la intensidad y el carcter agradable o desagradable del olor. Sirve para determinar si un olor se puede considerar molestia o no, esta metodologa se emplea en los EE. UU.
          .tarjeta.bg-yellow.p-3(x="89%" y="57%" numero="+")
            .h5.mb-2 Narices electrnicas
            p.mb-0 Son una serie de sensores de distintos materiales metlicos que reaccionan de forma distinta a un mismo componente qumico. Cuando una muestra olorosa entra en contacto con estos, se produce una respuesta qumica que se evala de forma conjunta para la totalidad de los sensores. La respuesta se relaciona con la intensidad o concentracin del olor a partir de los valores obtenidos por panelistas humanos que trasladan los resultados a una base de datos que queda registrada.
          .tarjeta.bg-yellow.p-3(x="96%" y="75%" numero="+")
            .h5.mb-2 Espectrmetros infrarrojos
            p.mb-0 En esta tcnica se emplean los Espectrmetros, en dcadas anteriores se han desarrollado mejores tecnologas para anlisis de compuestos qumicos, como el espectrmetro Transformador Infrarrojo de Paso Abierto de Fourier (Open-Path Fourier transforminfrared / OP-FTIR), el cual ofrece monitoreo remoto, anlisis multicompuestos rpidos y mediciones promedio. Esta tcnica ha sido aplicada exitosamente en la identificacin de fuentes de emisin.
          .tarjeta.bg-yellow.p-3(x="81%" y="94%" numero="+")
            .h5.mb-2 Modelizacin
            p.mb-0 Es el mtodo ms empleado para valorar la contaminacin por olores generada por una fuente. Se basa en el uso de modelos matemticos de dispersin de contaminantes. La modelizacin permite evaluar el impacto odorfero que una instalacin, existente o futura, tiene o tendr sobre su entorno prximo. A partir de los datos meteorolgicos de la zona se puede efectuar la modelizacin. El resultado del modelo es la obtencin de la concentracin de olor en inmisin. Esta concentracin puede representarse mediante lneas con un mismo valor de concentracin de olor: las isodoras.



    .row.justify-content-center.mb-2
      .col-md-4.col-9.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img20.png' alt='')
      .col-md-8.mb-4(data-aos="fade-left")
        .bg-primario.tarjeta.mb-4
          .row.align-items-center.p-4.p-md-0
            .col-md-3.col-4
              img.pt-lg-3.px-lg-4.ps-md-3(src='@/assets/curso/temas/tema2/icono2.svg' alt='')
            .col-md-9.py-4.text-white.pe-md-5
              h5 Formacin y emisin de olores
              p.mb-0 Se sugiere revisar el documento audiovisual Formacin y emisin de olores: caracterizacin y evaluacin de impacto que se encuentra en el material de apoyo, para fortalecer lo hasta ahora aprendido.
        p La evaluacin de olores ofensivos a travs de mtodos sensoriales se basa en la determinacin de la concentracin del olor por olfatometra dinmica, metodologa desarrollada en la NTC 5880 Calidad del Aire. Determinacin de la concentracin del olor por olfatometra dinmica correspondiente con la Norma Europea EN 13725 estandarizada por el Comit Europeo de Normalizacin. (MinAmbiente, 2014, p.31)
        p La norma establece un mtodo para la determinacin objetiva de la concentracin de olor de una muestra gaseosa usando olfatometra dinmica con evaluadores humanos y la velocidad de emisin de olores que emanan de fuentes puntuales y/o fuentes superficiales. La unidad de medida es la unidad de olor europea por metro cbico: OU<sub>E</sub> /m<sup>3</sup>. La concentracin de olor se expresa en trminos de mltiplos del umbral de deteccin. En cuanto al campo de aplicacin la norma europea, citado en MinAmbiente (2014, p. 31), incluye:
        ul.lista-ul--color.color-secundario.ps-3.ps-md-4.fa-ul
          li.mb-1
            i.fas.fa-check
            span La medida de la concentracin msica del umbral de deteccin de las sustancias olorosas puras en #[b g/m<sup>3</sup>]
          li.mb-1
            i.fas.fa-check
            span La medida de la concentracin de olor de mezclas de sustancias olorosas en <b>OU<sub>E</sub> /m<sup>3</sup></b>
          li.mb-1
            i.fas.fa-check
            span La medida de la velocidad de emisin de emisiones olorosas de fuentes puntuales y fuentes superficiales (con y sin un flujo hacia el exterior), incluyendo pre-dilucin durante el muestreo.
          li.mb-0
            i.fas.fa-check
            span El muestreo de sustancias olorosas de emisiones de alta humedad y temperatura (hasta 200C).

    p.mb-4(data-aos="fade") Para resumir los mtodos aplicables para la medicin de mezclas de olores ofensivos, el protocolo de olores ofensivos del MADS (MinAmbiente, 2014), se sintetizan en la siguiente tabla:

    .row.justify-content-center.mb-5
      .col-lg-9(data-aos="fade-down")
        .titulo-sexto.color-acento-contenido.mb-2
          p.mb-0 #[b Tabla 3.] Mtodos para la medicin de mezclas de sustancias, segn el tipo de fuente
        .tabla-a.tema5.color-secundario
          table
            caption 
              p.mb-0 Adaptado de MinAmbiente, (2014, p.33)
            thead
              tr.bg-secundario
                th Tipo de fuente
                th Toma de muestra
                th Anlisis de laboratorio
            tbody
              tr
                td Puntual
                td.text-center NTC 5880
                td(rowspan="3").text-center NTC 5880
              tr.bg-green7
                td Difusa
                td.text-center NTC 5880
              tr
                td Fugitiva 
                td.text-center NTC 6012


    p.mb-4(data-aos="fade") Ahora bien, la primera actividad a realizar que es considerada de mayor importancia para abordar cualquier problema de malos olores, es la identificacin de las posibles fuentes de olor y las caractersticas de las emisiones de los olores ofensivos. Cuando se realizan la toma de muestras para el anlisis por olfatometra dinmica, dependiendo de la fuente se realizan los siguientes tipos de muestreos.

    .row.justify-content-center.mb-2
      .col-md-6.mb-4(data-aos="fade-right")
        AcordionA(tipo="b" clase-tarjeta="tarjeta bg-green6")
          .row(titulo="Muestreo de fuentes puntuales")
            p Se utiliza el mtodo de la sonda. Este mtodo consiste en la aspiracin mediante una sonda de una fraccin alcuota del gas emitido por la fuente (Ductos o Chimeneas). (Snchez, J.I., Pea, A.A. & Martnez, J.V., 2008)
          .row(titulo="Muestreo de fuentes difusas aireadas")
            p Para este tipo de fuentes (reactor biolgico de una depuradora o un biofiltro - superficiales activas) se utiliza el mtodo de la Campana. En estas situaciones se coloca una campana de base cuadrada (1m2) sobre la superficie a muestrear, de manera que, a la salida de esta, la muestra es succionada mediante una sonda. (Snchez, J.I., Pea, A.A. & Martnez, J.V., 2008)
          .row(titulo="Muestreo de fuentes difusas no aireadas")
            p Para este tipo de fuentes (decantador de una depuradora o una parva o pila de compost  superficiales pasivas) se utiliza el mtodo del tnel del viento o cmara de flujo (conocida tambin como Caja Lindvall). Para ello, mediante un soplante, se insufla un caudal conocido de aire previamente filtrado, a travs de la cmara de flujo, tomando la muestra mediante una sonda a la salida de esta.
      .col-md-6.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img21.png' alt='')

    p.mb-4(data-aos="fade") En el caso de que, en cualquiera de las tcnicas de muestreo, se requiera llevar a cabo una dilucin en campo, el equipo debe cumplir con los mismos requisitos de exactitud y precisin, que se aplican al olfatmetro.

    .row.justify-content-center.mb-2
      .col-md-4.col-9.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img22.png' alt='')
      .col-md-8.mb-4(data-aos="fade-left")
        p Por otro lado, durante el muestreo es necesario la medida de una serie de parmetros (temperatura, presin atmosfrica, caudal, humedad, etc.) que se utilizarn posteriormente para el clculo de la emisin de olor de cada foco. Para asegurar la calidad y fiabilidad de las medidas, los instrumentos de medida tambin deben pasar unas calibraciones peridicas y cumplir los correspondientes criterios de calidad.
        .bg-primario.tarjeta.mb-4
          .row.align-items-center.p-4.p-md-0
            .col-md-2.col-4
              img.pt-md-3.ps-md-3(src='@/assets/curso/temas/tema2/icono3.svg' alt='')
            .col-md-10.py-4.text-white.pe-md-5
              h5 Formacin y emisin de olores 
              p.mb-0 Para profundizar en el tema se sugiere consultar en el material de apoyo el libro Contaminacin Ambiental por Olores. Fundamentos Bsicos. 
        .bloque-texto-b.bg-green9.box-shadow.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            p Por definicin cuando se hace referencia a olores ofensivos, necesariamente se estn describiendo dos condiciones: la presencia de una o ms sustancias olorosas en el aire y un receptor sensible. En este contexto, es el receptor sensible quien acta como indicador de una situacin dada por olores ofensivos en el ambiente. La materializacin de dicha situacin se da en cuanto dicho receptor involucra a las Autoridades Ambientales a travs de una queja. 
            br
            br
            h5.mb-0 - (MinAmbiente, 2014, p.8)
            i.fas.fa-quote-right
          

    p(data-aos="fade") Las quejas son el medio por el cual, la comunidad puede manifestar su inconformidad por las molestias que generan los olores, sin embargo, para calificar una queja como vlida es necesario que se verifiquen algunos aspectos que le permitan a la autoridad ambiental determinar de manera objetiva la existencia real de una afectacin por olores ofensivos.
    p.mb-4.mb-md-5(data-aos="fade") As, con el propsito de establecer una herramienta que permita minimizar los posibles sesgos en las quejas, se aplica una encuesta estandarizada que corresponde con la NTC 6012-1. A continuacin se resume el contenido de la misma y los aspectos a tener en cuenta para su aplicacin.

    .row.justify-content-center.mb-5
      .col-lg-5(data-aos="fade-down")
        .titulo-sexto.color-acento-contenido.mb-2
          p.mb-0 #[b Figura 3] Esquema de vigilancia basada en quejas y reclamos.
        figure
          img(src='@/assets/curso/temas/tema2/infog.svg' alt='ATENCIN A QUEJAS POR EXPOSICION A OLORES OFENSIVOS. ENCUESTA DE PERCEPCIN')
        figcaption.mt-3
          p.mb-0 Tomado de Ministerio de Salud (2012, p.98)

    .row.justify-content-center.mb-2
      .col-md-4.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img23.png' alt='')
      .col-md-8.mb-4(data-aos="fade-left")
        .bloque-texto-b.bg-green9.box-shadow.p-4
          .bloque-texto-b__texto
            i.fas.fa-quote-left
            p La Evaluacin sicomtrica de las molestias por olores describe mtodos de investigacin para establecer los niveles de molestia existente, para lo cual incluye una poblacin control, determina herramientas para estimar reas de poblacin afectadas y comparar resultados de modelos de dispersin con la realidad descrita por la poblacin. El alcance de la NTC 6012-1 permite obtener un panorama objetivo de una presunta problemtica por olores, as como la evaluacin de la eficacia de las medidas tomadas para la prevencin y/o control de las emisiones.
            br
            br
            h5.mb-0 - (MinAmbiente, 2014, p.9)
            i.fas.fa-quote-right

         

    .row.align-items-center
      .col-md-8.mb-4(data-aos="fade-right")
        p La planificacin de la encuesta se debe realizar con base en al menos una vista de campo en la que se verifique la existencia de una o ms potenciales actividades generadoras de olores ofensivos y el rea afectada identificando de manera aproximada el tamao de la poblacin en dicha rea. Se debe contar con los siguientes aspectos:
        ul.lista-ul--color.color-secundario.ps-3.ps-md-4.fa-ul
          li.mb-1
            i.fas.fa-check
            | Respecto a la seleccin de la zona control (sin afectacin por olores ofensivos), entendiendo que se trata de una poblacin de caractersticas similares a la de la poblacin afectada (edad, estrato socio econmico, cobertura de servicios de saneamiento bsico, tiempo de residencia y permanencia en las viviendas entre otros)
          li.mb-1
            i.fas.fa-check
            | Se debe definir el programa de aplicacin de las encuestas de tal modo que se cubra el rea definida.
          li.mb-1
            i.fas.fa-check
            | Se deben identificar los procesos que se realizan en dichas fuentes de emisin de olores ofensivos.
          li.mb-0
            i.fas.fa-check
            | De la aplicacin de la encuesta se debe obtener informacin de los grados de molestia, las zonas afectadas, poblacin expuesta.
          li
            i.fas.fa-check
            | El personal encargado de la aplicacin de las encuestas debe tener experiencia interactuando con comunidades.
        p Para establecer el procedimiento por atencin de quejas se establece el siguiente el siguiente procedimiento:
      .col-md-4.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/temas/tema2/img24.png' alt='')

    .row.justify-content-center.mb-2
      .col-md-3.col-9.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/tema2/img25.png' alt='')
      .col-md-9.mb-4(data-aos="fade-left")
        TabsA.tema5.color-acento-botones.mb-4(data-aos="fade-down") 
          .tarjeta.bg-green.p-4.p-md-5(titulo="Medicin de la molestia")
            h5 Medicin de la molestia
            p.mb-0 La NTC 6012-1 describe el modelo de cuestionario explicando el objetivo de cada una de las preguntas para la medicin de la molestia e informacin para el anlisis de los resultados de su aplicacin.
          .tarjeta.bg-green.p-4.p-md-5(titulo="Seleccin del rea")
            h5 Seleccin del rea
            p La NTC 6012-1 define los parmetros bsicos para la seleccin del rea o zona de afectacin en la que se aplicar la encuesta. Entre los que se cuenta el flujo volumtrico de la fuente o actividad generadora.
            p.mb-0 Esta informacin puede ser obtenida de referencias bibliogrficas considerando que se utiliza para hacer una estimacin preliminar de la zona. Algunas de las consideraciones ms relevantes a tener en cuenta para seleccionar la zona para la aplicacin de la encuesta son la velocidad y direccin del viento, la topografa y la nubosidad.
          .tarjeta.bg-green.p-4.p-md-5(titulo="Muestreo y calidad del procedimiento de recoleccin de los datos")
            h5 Muestreo y calidad del procedimiento de recoleccin de los datos
            p.mb-0 La NTC 6012-1 define criterios que se deben tener en cuenta respecto a las caractersticas sociodemogrficas de la muestra a evaluar.
          .tarjeta.bg-green.p-4.p-md-5(titulo="Evaluacin del cuestionario")
            h5 Evaluacin del cuestionario
            p.mb-0 La NTC 6012-1 describe en este numeral, para la escala de termmetro, la relacin matemtica entre los grados de molestia que se obtienen de las encuestas y las concentraciones de olor en los casos en los que la exposicin evaluada es uniforme en la zona de estudio.
          .tarjeta.bg-green.p-4.p-md-5(titulo="Anexos")
            h5 Anexos
            p.mb-0 Los anexos de la NTC6012-1 incluyen modelos para establecer contacto con los encuestados, ejemplos prcticos para la evaluacin de los datos obtenidos de la encuesta y el modelo de encuesta.
        
        p Finalmente, el PRIO se constituye entonces en la herramienta a travs de la cual, las actividades generadoras de olores ofensivos logran la prevencin y/o mitigacin de sus emisiones incorporando buenas prcticas o mejores tcnicas disponibles, las primeras entendidas como mtodos o tcnicas que han demostrado consistentemente resultados superiores a los obtenidos con otros medios y que se utilizan como punto de referencia y las segundas como la fase ms eficaz y avanzada de desarrollo de las actividades y de sus modalidades de explotacin, que demuestren la capacidad prctica de determinadas tcnicas para alcanzar el cumplimiento de los objetivos planteados en este caso para mitigar la generacin de olores ofensivos. (Ministerio de Ambiente, 2014, p.43).






