eran dependen del sitio y la ubicacin de las diversas fuentes que pueden ser considerados generadores olores, como criaderos de animales, plantas de tratamiento de aguas residuales, rellenos sanitarios entre otras. Con respecto al origen de los olores, en los diversos procesos, se podran clasificar en dos tipos:")]),_vm._m(45),_vm._m(46),_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade"}},[_vm._v("Se muestran a continuacin algunas fuentes generadoras de olores.")]),_c('AcordionA',{staticClass:"mb-5",attrs:{"tipo":"a","clase-tarjeta":"tarjeta bg-green6","data-aos":"fade-down"}},[_c('div',{staticClass:"row px-md-2",attrs:{"titulo":"Plantas de tratamiento de agua residual"}},[_c('div',{staticClass:"col-md-7"},[_c('p',[_vm._v("Las plantas de tratamiento de agua residual (PTAR) que tenga fallas en su diseo u operacin, sea de tipo fsico, qumico o biolgico, puede generar malos olores. El medio anaerobio es el ms susceptible a esta situacin, debido al metabolismo de las bacterias sulfato-reductoras, especialmente si en el agua residual existen altas concentraciones de sulfatos y sulfuros. (Ministerio de Salud, 2012 p. 24)")])]),_c('div',{staticClass:"col-md-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/acord-img1.png"),"alt":""}})])]),_c('div',{staticClass:"row px-md-2",attrs:{"titulo":"Rellenos sanitarios"}},[_c('div',{staticClass:"col-md-7"},[_c('p',[_vm._v("Desde la aparicin de las ciudades han existido problemas de salud pblica por la generacin de residuos, Los olores ofensivos provenientes de los rellenos sanitarios generalmente ocurren por un mal funcionamiento de los biofiltros o prcticas inadecuadas, como reutilizacin de reas previamente utilizadas o por descargas incontroladas de residuos. Esta percepcin de los malos olores no es permanente, sino que su presencia est asociada a las condiciones del clima y a la direccin de los vientos, siendo las horas nocturnas y los das clidos que siguen a las lluvias, cuando los olores son ms intensos. (Ministerio de Salud, 2012 p.28)")])]),_c('div',{staticClass:"col-md-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/acord-img2.png"),"alt":""}})])]),_c('div',{staticClass:"row px-md-2",attrs:{"titulo":"Curtido de cuero"}},[_c('div',{staticClass:"col-md-7"},[_c('p',[_vm._v("Las industrias de curtido y acabado de cueros, tradicionalmente han aparecido ante el mundo como algunas de las ms sucias y contaminantes debido al proceso para que este producto tenga el terminado que conocemos comercialmente. Como consecuencia, el agua residual de la industria de curtido tiene altas concentraciones de materia orgnica, compuestos de nitrgeno, sulfuros, slidos suspendidos, compuestos de cromo y pH elevado. ")]),_c('p',[_vm._v("La alta carga de materia orgnica provoca la creacin de condiciones anaerobias en cuerpos o cursos de agua, debido al elevado consumo de oxgeno disuelto. Estas condiciones, adems de afectar la vida acutica, favorecen la produccin de algunos gases nocivos como cido sulfhdrico y metano, indicadores cualitativos para la generacion de olores ofensivos de muy mal carcter. (Ministerio de Salud, 2012 p. 30)")]),_c('p',[_vm._v("Generalmente los malos olores provienen de un inadecuado control en las diferentes actividades, las mas significativas son: el pelambre, descarne, la limpieza de equipos y recipientes y el vertimiento de efluentes lquidos y slidos a los cursos de agua.")])]),_c('div',{staticClass:"col-md-5"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/acord-img3.png"),"alt":""}})])]),_c('div',{staticClass:"row px-md-2",attrs:{"titulo":"Industria petroqumica"}},[_c('div',{staticClass:"col-md-7"},[_c('p',[_vm._v("En la industria petrolera, los yacimientos de crudo contienen contaminantes entre los cuales se encuentra el azufre, el cual al combinarse con molculas de hidrogeno forma el cido sulfhdrico, que es liberado de los hidrocarburos en forma de gas al suministrarles calor. Existen microorganismos anaerobios capaces de utilizar compu